<template>
  <v-container no-gutters>
    <v-row>
      <v-col></v-col>
      <v-col class="text-center" cols="8" md="11" lg="10" xl="8">
        <v-card
          v-if="!loading"
          style="height: calc(100vh - 250px); overflow-y: auto"
        >
          <v-form ref="helpForm">
            <v-card-title>Please fill out the form:</v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <v-select
                v-model="helpObj.queType"
                dense
                :items="labOptions"
                label="I need:"
                outlined
              ></v-select>
              <v-text-field
                v-model="helpObj.title"
                dense
                :rules="[rules.required, rules.notempty]"
                label="Please enter Lab/Coursework Title:"
              ></v-text-field>
              <v-checkbox
                v-if="this.profOnlyQue"
                v-model="helpObj.requireProf"
                dense
                label="I need to speak to the Professor"
                hint="Warning: Wait times may be longer"
                persistent-hint
              />
              <v-textarea
                v-if="helpObj.queType == 'Help'"
                label="Description:"
                v-model="helpObj.desc"
                :rules="[rules.required]"
                counter="200"
                maxlength="200"
                auto-grow
              ></v-textarea>
            </v-card-text>
            <v-card-actions class="justify-center">
              <v-btn
                class="mb-2 green white--text"
                @click="getHelp"
                :loading="loading"
                :disabled="loading"
              >
                Start Request
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
        <v-card v-else>
          <v-card-title> You have joined the queue... </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <p class="text-h3">
              People in queue:
              <span class="purple--text lighten-text-1">{{ queLength }}</span>
            </p>
            <p class="text-h3">
              You are
              <span class="purple--text lighten-text-1">{{ quePosition }}</span>
              in the queue
            </p>
            <p class="text-h3">
              Average Wait Time:
              <span class="purple--text lighten-text-1">{{
                averageTime | mmss
              }}</span>
            </p>
          </v-card-text>
          <v-card-actions class="justify-center">
            <v-btn class="mb-2 deep-orange lighten-2" dark @click="cancelHelp">
              <v-icon class="mr-1">mdi-cancel</v-icon>Cancel
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col></v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      labOptions: ["Help", "Marking"],
      helpObj: {
        queType: "Help",
        title: "",
        desc: "",
        requireProf: false,
      },
      que: [],
      averageTime: 0,
      loading: false,
      rules: {
        required: (value) => !!value || "Required Field",
        notempty: (value) => value.length > 0 || "Required Field",
      },
    };
  },
  computed: {
    ...mapGetters({
      lab_id: "socket/lab_id",
      labCode: "socket/labCode",
      username: "socket/username",
      privateChat: "socket/privateChat",
      gettingSupport: "socket/gettingSupport",
      queWaiting: "socket/queWaiting",
      profOnlyQue: "socket/profOnlyQue",
    }),
    queLength() {
      return this.que.length;
    },
    quePosition() {
      const i = this.que.findIndex((x) => x.socketid === this.$socket.id) + 1;
      const j = i % 10;
      const k = i % 100;
      if (j == 1 && k != 11) {
        return i + "st";
      }
      if (j == 2 && k != 12) {
        return i + "nd";
      }
      if (j == 3 && k != 13) {
        return i + "rd";
      }
      return i + "th";
    },
  },
  watch: {
    "helpObj.queType": {
      handler() {
        this.helpObj.desc = "";
      },
      deep: true,
    },
  },
  methods: {
    async getHelp() {
      if (this.$refs.helpForm.validate()) {
        this.loading = true;
        const queData = {
          ...this.helpObj,
          lab_id: this.lab_id,
          labCode: this.labCode,
          socketid: this.$socket.id,
        };
        await this.$socket.emit("joinQue", queData);
        this.$store.dispatch("socket/joinQue").then(() => {
          this.helpObj = {
            queType: "Help",
            title: "",
            desc: "",
            requireProf: false,
          };
        });
      }
    },
    async cancelHelp() {
      const queData = {
        lab_id: this.lab_id,
        labCode: this.labCode,
        socketid: this.$socket.id,
      };
      this.$store.dispatch("socket/leaveQue").then(() => {
        this.$socket.emit("leaveQue", queData);
        this.loading = false;
      });
    },
  },
  sockets: {
    updateQue: function (data) {
      this.que = data.que;
      this.averageTime = data.averageTime;
    },
    startHelp: function (data) {
      this.$store.dispatch("socket/privateChatInfo", data).then(() => {
        const helperInfo = {
          sendTo: this.gettingSupport.reciever,
          reciever: this.$socket.id,
          privateChat: this.privateChat,
          peerid: this.$peer._id,
        };
        this.$socket.emit("startHelper", helperInfo);
      });
    },
  },
  mounted() {
    axios(process.env.VUE_APP_ENDPOINT, {
      method: "POST",
      data: {
        query: `
              query getQue($lab_id: ID!){
                getQue(lab_id: $lab_id){
                  que {
                    socketid
                  }
                }
              }
            `,
        variables: {
          lab_id: this.lab_id,
        },
      },
    })
      .then((res) => {
        this.que = res.data.data.getQue.que;
      })
      .catch((error) => {
        if (error.response) {
          this.errorList = error.response.data.errors;
          for (let i = 0; i < this.errorList.length; i++) {
            this.$toast.error(this.errorList[i].message);
          }
        } else {
          console.log("Error", error.message);
        }
      });
  },
};
</script>